'use client'

import { useState, useEffect } from 'react'

interface Friend {
  id: string
  username: string
  discriminator: string
  avatar: string | null
  type: number
  nickname?: string
}

export default function RemoverAmizadesPage() {
  const [friends, setFriends] = useState<Friend[]>([])
  const [selectedFriends, setSelectedFriends] = useState<Set<string>>(new Set())
  const [isLoading, setIsLoading] = useState(false)
  const [isLoadingFriends, setIsLoadingFriends] = useState(false)
  const [error, setError] = useState('')
  const [success, setSuccess] = useState('')
  const [hasToken, setHasToken] = useState(false)
  const [searchTerm, setSearchTerm] = useState('')
  const [showConfirmation, setShowConfirmation] = useState(false)

  useEffect(() => {
    const token = localStorage.getItem('discordToken')
    setHasToken(!!token)
    if (token) {
      loadFriends()
    }
  }, [])

  const loadFriends = async () => {
    setIsLoadingFriends(true)
    setError('')
    
    try {
      const token = localStorage.getItem('discordToken')
      if (!token) {
        setError('Token n√£o encontrado')
        return
      }

      const response = await fetch('https://discord.com/api/v10/users/@me/relationships', {
        headers: {
          'Authorization': token,
          'Content-Type': 'application/json'
        }
      })

      if (!response.ok) {
        throw new Error(`Erro ${response.status}: ${response.statusText}`)
      }

      const data = await response.json()
      const friendsList = data.filter((rel: any) => rel.type === 1) // Tipo 1 = amigos
      setFriends(friendsList)
    } catch (err: any) {
      setError(`Erro ao carregar amigos: ${err.message}`)
    } finally {
      setIsLoadingFriends(false)
    }
  }

  const removeFriend = async (friendId: string) => {
    const token = localStorage.getItem('discordToken')
    if (!token) {
      throw new Error('Token n√£o encontrado')
    }

    const response = await fetch(`https://discord.com/api/v10/users/@me/relationships/${friendId}`, {
      method: 'DELETE',
      headers: {
        'Authorization': token,
        'Content-Type': 'application/json'
      }
    })

    if (!response.ok) {
      throw new Error(`Erro ${response.status}: ${response.statusText}`)
    }
  }

  const handleRemoveSelected = async () => {
    if (selectedFriends.size === 0) {
      setError('Selecione pelo menos um amigo para remover')
      return
    }

    setIsLoading(true)
    setError('')
    setSuccess('')

    try {
      let removedCount = 0
      let errorCount = 0

      for (const friendId of selectedFriends) {
        try {
          await removeFriend(friendId)
          removedCount++
          
          // Delay entre remo√ß√µes para evitar rate limit
          await new Promise(resolve => setTimeout(resolve, 1000))
        } catch (err) {
          errorCount++
          console.error(`Erro ao remover amigo ${friendId}:`, err)
        }
      }

      setSuccess(`${removedCount} amigo(s) removido(s) com sucesso!${errorCount > 0 ? ` ${errorCount} erro(s) ocorreram.` : ''}`)
      setSelectedFriends(new Set())
      setShowConfirmation(false)
      
      // Recarregar lista de amigos
      await loadFriends()
    } catch (err: any) {
      setError(`Erro ao remover amigos: ${err.message}`)
    } finally {
      setIsLoading(false)
    }
  }

  const handleRemoveAll = async () => {
    setIsLoading(true)
    setError('')
    setSuccess('')

    try {
      let removedCount = 0
      let errorCount = 0

      for (const friend of friends) {
        try {
          await removeFriend(friend.id)
          removedCount++
          
          // Delay entre remo√ß√µes para evitar rate limit
          await new Promise(resolve => setTimeout(resolve, 1000))
        } catch (err) {
          errorCount++
          console.error(`Erro ao remover amigo ${friend.id}:`, err)
        }
      }

      setSuccess(`${removedCount} amigo(s) removido(s) com sucesso!${errorCount > 0 ? ` ${errorCount} erro(s) ocorreram.` : ''}`)
      setShowConfirmation(false)
      
      // Recarregar lista de amigos
      await loadFriends()
    } catch (err: any) {
      setError(`Erro ao remover todos os amigos: ${err.message}`)
    } finally {
      setIsLoading(false)
    }
  }

  const toggleFriendSelection = (friendId: string) => {
    const newSelected = new Set(selectedFriends)
    if (newSelected.has(friendId)) {
      newSelected.delete(friendId)
    } else {
      newSelected.add(friendId)
    }
    setSelectedFriends(newSelected)
  }

  const selectAll = () => {
    const filteredFriends = friends.filter(friend => 
      friend.username.toLowerCase().includes(searchTerm.toLowerCase()) ||
      friend.discriminator.includes(searchTerm)
    )
    setSelectedFriends(new Set(filteredFriends.map(f => f.id)))
  }

  const deselectAll = () => {
    setSelectedFriends(new Set())
  }

  const getAvatarUrl = (friend: Friend) => {
    if (friend.avatar) {
      return `https://cdn.discordapp.com/avatars/${friend.id}/${friend.avatar}.png?size=64`
    }
    return `https://cdn.discordapp.com/embed/avatars/${parseInt(friend.discriminator) % 5}.png`
  }

  const filteredFriends = friends.filter(friend => 
    friend.username.toLowerCase().includes(searchTerm.toLowerCase()) ||
    friend.discriminator.includes(searchTerm)
  )

  if (!hasToken) {
    return (
      <div className="max-w-2xl mx-auto text-center space-y-8 animate-fade-in">
        <div className="space-y-4">
          <div className="w-20 h-20 bg-gradient-to-r from-red-500 to-red-600 rounded-full flex items-center justify-center mx-auto">
            <span className="text-3xl">üîí</span>
          </div>
          <h1 className="text-3xl font-bold text-white">Token N√£o Configurado</h1>
          <p className="text-gray-300 text-lg">
            Configure seu token Discord para usar a ferramenta de remo√ß√£o de amizades.
          </p>
        </div>
        <div className="flex flex-col sm:flex-row gap-4 justify-center">
          <a 
            href="/config" 
            className="px-8 py-4 bg-gradient-to-r from-purple-600 to-purple-700 hover:from-purple-500 hover:to-purple-600 rounded-lg font-semibold transition-all duration-300 transform hover:scale-105"
          >
            Configurar Token
          </a>
          <a 
            href="/tutorial" 
            className="px-8 py-4 border border-purple-600 hover:bg-purple-600/10 rounded-lg font-semibold transition-all duration-300 transform hover:scale-105"
          >
            Ver Tutorial
          </a>
        </div>
      </div>
    )
  }

  return (
    <div className="max-w-6xl mx-auto space-y-8 animate-fade-in">
      <div className="text-center space-y-4">
        <h1 className="text-4xl font-bold bg-gradient-to-r from-red-400 to-red-600 bg-clip-text text-transparent">
          Remover Amizades
        </h1>
        <p className="text-gray-300 text-lg">
          Gerencie sua lista de amigos removendo contatos selecionados ou todos de uma vez.
        </p>
      </div>

      {/* Controles */}
      <div className="bg-gray-900/50 border border-red-800/30 rounded-xl p-6 space-y-4">
        <div className="flex flex-col md:flex-row gap-4 items-center justify-between">
          <div className="flex-1 max-w-md">
            <input
              type="text"
              placeholder="Buscar amigos..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              className="w-full p-3 bg-gray-800/50 border border-gray-700 rounded-lg focus:border-red-500 focus:ring-2 focus:ring-red-500/20 transition-all duration-300 text-white placeholder-gray-400"
            />
          </div>
          
          <div className="flex gap-2">
            <button
              onClick={loadFriends}
              disabled={isLoadingFriends}
              className="px-4 py-2 bg-blue-600 hover:bg-blue-500 disabled:bg-gray-600 rounded-lg font-medium transition-all duration-300 transform hover:scale-105 disabled:scale-100"
            >
              {isLoadingFriends ? 'Carregando...' : 'Atualizar'}
            </button>
            
            <button
              onClick={selectAll}
              className="px-4 py-2 bg-green-600 hover:bg-green-500 rounded-lg font-medium transition-all duration-300 transform hover:scale-105"
            >
              Selecionar Todos
            </button>
            
            <button
              onClick={deselectAll}
              className="px-4 py-2 bg-gray-600 hover:bg-gray-500 rounded-lg font-medium transition-all duration-300 transform hover:scale-105"
            >
              Desmarcar Todos
            </button>
          </div>
        </div>

        <div className="flex flex-col md:flex-row gap-4">
          <button
            onClick={() => setShowConfirmation(true)}
            disabled={isLoading || selectedFriends.size === 0}
            className="flex-1 px-6 py-3 bg-gradient-to-r from-red-600 to-red-700 hover:from-red-500 hover:to-red-600 disabled:from-gray-600 disabled:to-gray-700 rounded-lg font-semibold transition-all duration-300 transform hover:scale-105 disabled:scale-100 disabled:cursor-not-allowed"
          >
            {isLoading ? 'Removendo...' : `Remover Selecionados (${selectedFriends.size})`}
          </button>
          
          <button
            onClick={() => setShowConfirmation(true)}
            disabled={isLoading || friends.length === 0}
            className="flex-1 px-6 py-3 bg-gradient-to-r from-red-700 to-red-800 hover:from-red-600 hover:to-red-700 disabled:from-gray-600 disabled:to-gray-700 rounded-lg font-semibold transition-all duration-300 transform hover:scale-105 disabled:scale-100 disabled:cursor-not-allowed"
          >
            {isLoading ? 'Removendo...' : `Remover Todos (${friends.length})`}
          </button>
        </div>
      </div>

      {/* Lista de Amigos */}
      <div className="bg-gray-900/50 border border-red-800/30 rounded-xl p-6">
        <div className="flex items-center justify-between mb-6">
          <h2 className="text-2xl font-bold text-white">Lista de Amigos</h2>
          <span className="px-3 py-1 bg-red-600/20 text-red-300 text-sm rounded-full">
            {filteredFriends.length} de {friends.length}
          </span>
        </div>

        {isLoadingFriends ? (
          <div className="text-center py-8">
            <div className="loading-spinner mx-auto mb-4"></div>
            <p className="text-gray-400">Carregando amigos...</p>
          </div>
        ) : filteredFriends.length === 0 ? (
          <div className="text-center py-8 space-y-4">
            <div className="w-16 h-16 bg-gray-800 rounded-full flex items-center justify-center mx-auto">
              <span className="text-2xl">üë•</span>
            </div>
            <p className="text-gray-400">
              {friends.length === 0 ? 'Nenhum amigo encontrado' : 'Nenhum amigo corresponde √† busca'}
            </p>
          </div>
        ) : (
          <div className="grid gap-4 max-h-96 overflow-y-auto">
            {filteredFriends.map((friend, index) => (
              <div
                key={friend.id}
                className={`flex items-center space-x-4 p-4 bg-gray-800/50 border rounded-lg transition-all duration-300 animate-slide-in-left ${
                  selectedFriends.has(friend.id) 
                    ? 'border-red-600/50 bg-red-900/10' 
                    : 'border-gray-700 hover:border-red-600/30'
                }`}
                style={{ animationDelay: `${index * 0.05}s` }}
              >
                <input
                  type="checkbox"
                  checked={selectedFriends.has(friend.id)}
                  onChange={() => toggleFriendSelection(friend.id)}
                  className="w-4 h-4 text-red-600 bg-gray-700 border-gray-600 rounded focus:ring-red-500 focus:ring-2"
                />
                
                <img
                  src={getAvatarUrl(friend)}
                  alt={`${friend.username} avatar`}
                  className="w-10 h-10 rounded-full"
                  onError={(e) => {
                    e.currentTarget.src = `https://cdn.discordapp.com/embed/avatars/${parseInt(friend.discriminator) % 5}.png`
                  }}
                />
                
                <div className="flex-1">
                  <div className="flex items-center space-x-2">
                    <span className="font-medium text-white">{friend.username}</span>
                    <span className="text-gray-400">#{friend.discriminator}</span>
                  </div>
                  {friend.nickname && (
                    <p className="text-sm text-gray-400">Apelido: {friend.nickname}</p>
                  )}
                </div>
                
                <div className="text-xs text-gray-500">
                  ID: {friend.id}
                </div>
              </div>
            ))}
          </div>
        )}
      </div>

      {/* Modal de Confirma√ß√£o */}
      {showConfirmation && (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50">
          <div className="bg-gray-900 border border-red-600/30 rounded-xl p-6 max-w-md w-full mx-4 space-y-4">
            <div className="text-center space-y-2">
              <div className="w-16 h-16 bg-red-600/20 rounded-full flex items-center justify-center mx-auto">
                <span className="text-3xl">‚ö†Ô∏è</span>
              </div>
              <h3 className="text-xl font-bold text-white">Confirmar Remo√ß√£o</h3>
              <p className="text-gray-300">
                {selectedFriends.size > 0 
                  ? `Tem certeza que deseja remover ${selectedFriends.size} amigo(s) selecionado(s)?`
                  : `Tem certeza que deseja remover TODOS os ${friends.length} amigos?`
                }
              </p>
              <p className="text-red-400 text-sm">
                Esta a√ß√£o n√£o pode ser desfeita!
              </p>
            </div>
            
            <div className="flex gap-4">
              <button
                onClick={() => setShowConfirmation(false)}
                className="flex-1 px-4 py-2 bg-gray-600 hover:bg-gray-500 rounded-lg font-medium transition-all duration-300"
              >
                Cancelar
              </button>
              <button
                onClick={selectedFriends.size > 0 ? handleRemoveSelected : handleRemoveAll}
                disabled={isLoading}
                className="flex-1 px-4 py-2 bg-red-600 hover:bg-red-500 disabled:bg-gray-600 rounded-lg font-medium transition-all duration-300"
              >
                {isLoading ? 'Removendo...' : 'Confirmar'}
              </button>
            </div>
          </div>
        </div>
      )}

      {/* Mensagens de Status */}
      {error && (
        <div className="bg-red-900/30 border border-red-600/30 rounded-lg p-4 text-red-300 animate-slide-in-left">
          {error}
        </div>
      )}

      {success && (
        <div className="bg-green-900/30 border border-green-600/30 rounded-lg p-4 text-green-300 animate-slide-in-left">
          {success}
        </div>
      )}

      {/* Aviso */}
      <div className="bg-yellow-900/20 border border-yellow-600/30 rounded-xl p-6 space-y-3">
        <div className="flex items-center space-x-2">
          <span className="text-2xl">‚ö†Ô∏è</span>
          <h3 className="text-lg font-semibold text-yellow-300">Importante</h3>
        </div>
        <div className="text-yellow-200 space-y-2 text-sm">
          <p>‚Ä¢ A remo√ß√£o de amigos √© permanente e n√£o pode ser desfeita</p>
          <p>‚Ä¢ Use esta ferramenta com cuidado para n√£o remover amigos importantes</p>
          <p>‚Ä¢ H√° um delay de 1 segundo entre cada remo√ß√£o para evitar rate limiting</p>
          <p>‚Ä¢ Voc√™ pode precisar reenviar solicita√ß√µes de amizade para reconectar</p>
        </div>
      </div>
    </div>
  )
}
